.container{
    position: absolute;
    width: 230px;
    height: 200px;
    background-color: var(--primary-background-color);
    z-index: 100;
    padding: 25px;
    border: 2px solid var(--accent-text-color);
    border-radius: 10px;
    top: 90px;
    right: 40px;
}

.logOut{
        display: flex;
        align-items: center;
        gap: 11px;
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        color: var(--button-text-color);
        padding: 10px 35px;
        background-color: var(--button-background-color);
        box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
        border-radius: 16px;
        width: fit-content;

        &:focus {
            background: #2B78EF;
            box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
        }
        &:hover {
            background: #2B78EF;
            box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);}
        & > svg {
            width: 20px;
            height: 20px;
            stroke: #ffffff;
            fill: none;
    }

}

.account{
        display: inline-flex;
        align-items: center;
        font-family: var(--primary-font);
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        text-decoration: none;
        color: var(--not-active-item-sidebar-text-color);
        padding: 10px 20px;
        gap: 8px;
        margin-bottom: 10px;
        border-radius: 16px;
        width: fit-content;
        & > svg {
            width: 24px;
            height: 24px;
            stroke: var(--not-active-item-sidebar-text-color);
            fill: none;
            }
        
        &:focus {
        color: #fff;
        background: var(--active-item-sidebar-background);
        box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
        & > svg {
            stroke: #fff;
            }
        }
        &:hover {
        color: var(--accent-text-color);
        background: var(--active-item-sidebar-background);
        box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
        & > svg {
            stroke: var(--accent-text-color);
            }
        }
}

.userName {
    font-weight: 700;
    font-size: 18px;
    line-height: 1;
    color:var(--userpage-text)
}

.userAvatar {
    height: 44px;
    width: 44px;
    border: 1.8px solid #3e85f3;
    border-radius: 50%;
    margin-right: 15px;
}


.user{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.userHeader{
    display: flex;
    align-items: center;
    gap: 14px;
    margin-left: auto;
    cursor: pointer;
}

.userNameHeader{
    font-weight: 700;
    font-size: 18px;
    line-height: 1;
    color:var(--userpage-text)
}

.userAvatarHeader{
    height: 44px;
    width: 44px;
    border: 1.8px solid #3e85f3;
    border-radius: 50%;
    margin-right: 15px;
}